import * as types from "../actions/actionTypes";
import initialState from "./initialState";

export default function ancillaryReducer(
  state = initialState.ancillaries,
  action
) {
  switch (action.type) {
    case types.LOAD_ANCILLARIES_SUCCESS:
      return action.ancillaries;
    case types.CREATE_ANCILLARY_SUCCESS:
      return [...state, { ...action.ancillary }];
    case types.UPDATE_ANCILLARY_SUCCESS:
      return state.map((ancillary) =>
        ancillary.id === action.ancillary.id ? action.ancillary : ancillary
      );
    default:
      return state;
  }
}
