import React, { Suspense } from "react";
import { Route, Switch, Redirect } from "react-router-dom";
import Header from "./common/Header";
import PropTypes from "prop-types";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

const HomePage = React.lazy(() => import("./home/HomePage"));
const AboutPage = React.lazy(() => import("./about/AboutPage"));
const PageNotFound = React.lazy(() => import("./PageNotFound"));
const FlightsPage = React.lazy(() => import("./flights/FlightsPage"));
const FlightDetailsPage = React.lazy(() =>
  import("./flights/FlightDetailsPage")
);
const PassengersPage = React.lazy(() => import("./passengers/PassengersPage"));
const PassengerDetailsPage = React.lazy(() =>
  import("./passengers/PassengerDetailsPage")
);
const StaffDashboardPage = React.lazy(() =>
  import("./dashboard/StaffDashboardPage")
);
const InFlightDetailsPage = React.lazy(() =>
  import("./flights/InFlightDetailsPage")
);
const AdminDashboardPage = React.lazy(() =>
  import("./dashboard/AdminDashboardPage")
);
const FlightsPageAdmin = React.lazy(() => import("./flights/FlightsPageAdmin"));
const PassengersPageAdmin = React.lazy(() =>
  import("./passengers/PassengersPageAdmin")
);
const ManagePassengerPage = React.lazy(() =>
  import("./passengers/ManagePassengerPage")
);
const AncillariesPage = React.lazy(() =>
  import("./ancillaries/AncillariesPage")
);
const EntryPage = React.lazy(() => import("./auxillaries/EntryPage"));

const checkStaffAuth = () => {
  const status = localStorage.getItem("auth");
  return status && status === "staff";
};

const checkAdminAuth = () => {
  const status = localStorage.getItem("auth");
  return status && status === "admin";
};

const AuthStaffRoute = ({ component: Component, ...rest }) => (
  <Route
    {...rest}
    render={(props) =>
      checkStaffAuth() ? (
        <Component {...props} />
      ) : (
        <Redirect to={{ pathname: "/" }} />
      )
    }
  />
);

const AuthAdminRoute = ({ component: Component, ...rest }) => (
  <Route
    {...rest}
    render={(props) =>
      checkAdminAuth() ? (
        <Component {...props} />
      ) : (
        <Redirect to={{ pathname: "/" }} />
      )
    }
  />
);

function App() {
  return (
    <div className="container-fluid">
      <Header
        onClick={(event) => {
          event.preventDefault();
          localStorage.removeItem("auth");
          window.location.reload(false);
        }}
      />
      <ToastContainer autoClose={3000} hideProgressBar />
      <Suspense fallback={<div>Loading...</div>}>
        <Switch>
          <Route
            exact
            path="/"
            render={(props) =>
              checkStaffAuth() ? (
                <Redirect to={{ pathname: "/staff/dashboard" }} />
              ) : checkAdminAuth() ? (
                <Redirect to={{ pathname: "/admin/dashboard" }} />
              ) : (
                <HomePage {...props} />
              )
            }
          />
          <Route exact path="/about" component={AboutPage} />
          <AuthStaffRoute path="/staff/flights" component={FlightsPage} />
          <AuthStaffRoute
            path="/staff/dashboard"
            component={StaffDashboardPage}
          />
          <AuthStaffRoute
            path="/staff/flight/:name"
            component={FlightDetailsPage}
          />
          <AuthStaffRoute
            path="/staff/in-flight/passengers/:name"
            component={PassengersPage}
          />
          <AuthStaffRoute
            exact
            path="/staff/in-flight/:name"
            component={InFlightDetailsPage}
          />
          <AuthStaffRoute
            path="/staff/passenger/:name/:pnr"
            component={PassengerDetailsPage}
          />
          <AuthStaffRoute
            path="/staff/passengers/:name"
            component={PassengersPage}
          />

          <AuthAdminRoute
            path="/admin/dashboard"
            component={AdminDashboardPage}
          />
          <AuthAdminRoute path="/admin/flights" component={FlightsPageAdmin} />
          <AuthAdminRoute
            path="/admin/flight/:name"
            component={FlightDetailsPage}
          />
          <AuthAdminRoute
            path="/admin/ancillaries/:name"
            component={AncillariesPage}
          />
          <AuthAdminRoute path="/admin/purchases/:name" component={EntryPage} />
          <AuthAdminRoute path="/admin/meals/:name" component={EntryPage} />
          <AuthAdminRoute
            path="/admin/passenger/:name/:pnr"
            component={ManagePassengerPage}
          />
          <AuthAdminRoute
            path="/admin/passenger/:name"
            component={ManagePassengerPage}
          />
          <AuthAdminRoute
            path="/admin/passengers/:name"
            component={PassengersPageAdmin}
          />
          <Route component={PageNotFound} />
        </Switch>
      </Suspense>
    </div>
  );
}

AuthAdminRoute.propTypes = {
  component: PropTypes.oneOfType([PropTypes.object, PropTypes.func]),
};

AuthStaffRoute.propTypes = {
  component: PropTypes.oneOfType([PropTypes.object, PropTypes.func]),
};

export default App;
